import { useState, useCallback } from 'react'
import apiService from '../services/apiService'
import { logger } from '../utils/logger'
import '../styles/Eliminar.css'

const CONFIRMATION_PHRASE = 'ELIMINAR'

const UserPreview = ({ user }) => (
  <div className="user-preview">
    <div className="preview-header">
      <span className="preview-icon" aria-hidden="true">üë§</span>
      <h4 className="preview-title">Informaci√≥n del Usuario a Eliminar</h4>
    </div>
    <div className="user-details">
      <div className="detail-item">
        <span className="detail-label">ID</span>
        <span className="detail-value">{user.id}</span>
      </div>
      <div className="detail-item">
        <span className="detail-label">Usuario</span>
        <span className="detail-value">{user.usuario || 'N/A'}</span>
      </div>
      <div className="detail-item">
        <span className="detail-label">Nombre Completo</span>
        <span className="detail-value">
          {user.nombre && user.apellido
            ? `${user.nombre} ${user.apellido}`
            : user.nombre || user.apellido || 'N/A'
          }
        </span>
      </div>
      <div className="detail-item">
        <span className="detail-label">Email</span>
        <span className="detail-value">{user.email || 'N/A'}</span>
      </div>
      <div className="detail-item">
        <span className="detail-label">Tel√©fono</span>
        <span className="detail-value">{user.telefono || 'N/A'}</span>
      </div>
      <div className="detail-item">
        <span className="detail-label">Ciudad</span>
        <span className="detail-value">{user.ciudad || 'N/A'}</span>
      </div>
      <div className="detail-item">
        <span className="detail-label">Especialidad</span>
        <span className="detail-value">{user.especialidad || 'N/A'}</span>
      </div>
      <div className="detail-item">
        <span className="detail-label">Instagram</span>
        <span className="detail-value">{user.instagram || 'N/A'}</span>
      </div>
    </div>
  </div>
)

const ConfirmationDialog = ({ user, confirmationText, onConfirmationChange, onConfirm, onCancel, isDeleting }) => {
  const isValid = confirmationText === CONFIRMATION_PHRASE

  return (
    <div className="confirmation-dialog" role="dialog" aria-labelledby="confirmation-title">
      <div className="dialog-header">
        <span className="dialog-icon" aria-hidden="true">‚ö†Ô∏è</span>
        <h4 id="confirmation-title" className="dialog-title">Confirmar Eliminaci√≥n</h4>
      </div>

      <div className="dialog-message">
        <p>
          <strong>¬°Advertencia!</strong> Esta acci√≥n no se puede deshacer.
          El usuario <strong>{user.usuario || `ID: ${user.id}`}</strong> ser√° eliminado permanentemente del sistema.
        </p>
        <p>
          Para continuar, escriba <strong>"{CONFIRMATION_PHRASE}"</strong> en el campo de abajo:
        </p>
      </div>

      <div className="confirmation-input-group">
        <label htmlFor="confirmation-input" className="confirmation-label">
          Escriba "{CONFIRMATION_PHRASE}" para confirmar:
        </label>
        <input
          id="confirmation-input"
          type="text"
          value={confirmationText}
          onChange={onConfirmationChange}
          className="confirmation-input"
          placeholder={CONFIRMATION_PHRASE}
          autoComplete="off"
          autoFocus
        />
        <div className="confirmation-help">
          Debe coincidir exactamente con "{CONFIRMATION_PHRASE}"
        </div>
      </div>

      <div className="form-actions">
        <button
          type="button"
          onClick={onCancel}
          className="cancel-button"
          disabled={isDeleting}
        >
          Cancelar
        </button>

        <button
          type="button"
          onClick={onConfirm}
          className="delete-button"
          disabled={!isValid || isDeleting}
        >
          {isDeleting ? (
            <>
              <div className="spinner" aria-hidden="true"></div>
              Eliminando...
            </>
          ) : (
            <>
              <span aria-hidden="true">üóëÔ∏è</span>
              Eliminar Usuario
            </>
          )}
        </button>
      </div>
    </div>
  )
}

const Eliminar = () => {
  const [userId, setUserId] = useState('')
  const [userToDelete, setUserToDelete] = useState(null)
  const [confirmationText, setConfirmationText] = useState('')
  const [showConfirmation, setShowConfirmation] = useState(false)
  const [isSearching, setIsSearching] = useState(false)
  const [isDeleting, setIsDeleting] = useState(false)
  const [successMessage, setSuccessMessage] = useState('')
  const [errorMessage, setErrorMessage] = useState('')

  const clearMessages = useCallback(() => {
    setSuccessMessage('')
    setErrorMessage('')
  }, [])

  const resetForm = useCallback(() => {
    setUserId('')
    setUserToDelete(null)
    setConfirmationText('')
    setShowConfirmation(false)
    setIsSearching(false)
    setIsDeleting(false)
  }, [])

  const searchUser = useCallback(async () => {
    if (!userId || userId.trim() === '') {
      setErrorMessage('Por favor, ingrese un ID de usuario v√°lido')
      return
    }

    try {
      setIsSearching(true)
      clearMessages()
      logger.info(`Searching for user with ID: ${userId}`)

      const response = await apiService.getUsers()
      const user = response.data?.find(u => u.id.toString() === userId.toString())

      if (user) {
        setUserToDelete(user)
        logger.info('User found for deletion', user)
      } else {
        setErrorMessage('Usuario no encontrado. Verifique el ID e intente nuevamente.')
        setUserToDelete(null)
      }
    } catch (err) {
      logger.error('Failed to search user', err)
      setErrorMessage(err.message || 'Error al buscar el usuario')
      setUserToDelete(null)
    } finally {
      setIsSearching(false)
    }
  }, [userId, clearMessages])

  const handleSearchSubmit = (e) => {
    e.preventDefault()
    searchUser()
  }

  const handleShowConfirmation = () => {
    setShowConfirmation(true)
    setConfirmationText('')
  }

  const handleCancelConfirmation = () => {
    setShowConfirmation(false)
    setConfirmationText('')
  }

  const handleDeleteUser = useCallback(async () => {
    if (!userToDelete || confirmationText !== CONFIRMATION_PHRASE) {
      return
    }

    try {
      setIsDeleting(true)
      clearMessages()
      logger.info(`Deleting user with ID: ${userToDelete.id}`)

      const response = await apiService.deleteUser(userToDelete.id)

      // If we reach here without error, deletion was successful
      setSuccessMessage(response.message || 'Usuario eliminado exitosamente')
      resetForm()
      logger.info('User deleted successfully')
    } catch (err) {
      logger.error('Failed to delete user', err)
      setErrorMessage(err.message || 'Error al eliminar el usuario')
      setIsDeleting(false)
    }
  }, [userToDelete, confirmationText, clearMessages, resetForm])

  const handleUserIdChange = (e) => {
    setUserId(e.target.value)
    clearMessages()
    setUserToDelete(null)
    setShowConfirmation(false)
  }

  const handleConfirmationTextChange = (e) => {
    setConfirmationText(e.target.value)
  }

  return (
    <div className="eliminar-container">
      <header className="eliminar-header">
        <h2 className="eliminar-title">Eliminar Usuario</h2>
        <p className="eliminar-subtitle">
          Elimine permanentemente un usuario del sistema
        </p>
      </header>

      <div className="warning-banner" role="alert">
        <span className="warning-icon" aria-hidden="true">‚ö†Ô∏è</span>
        <div>
          <strong>Precauci√≥n:</strong> Esta acci√≥n eliminar√° permanentemente el usuario y no se puede deshacer.
          Aseg√∫rese de tener el ID correcto antes de proceder.
        </div>
      </div>

      <div className="delete-card">
        {successMessage && (
          <div className="success-message" role="alert">
            <span aria-hidden="true">‚úÖ</span>
            {successMessage}
          </div>
        )}

        {errorMessage && (
          <div className="error-message" role="alert">
            <span aria-hidden="true">‚ùå</span>
            {errorMessage}
          </div>
        )}

        {!showConfirmation ? (
          <form className="delete-form" onSubmit={handleSearchSubmit}>
            <div className="form-group">
              <label htmlFor="userId" className="form-label">
                ID del Usuario a Eliminar
                <span className="required-indicator" aria-hidden="true"> *</span>
              </label>
              <input
                id="userId"
                type="number"
                value={userId}
                onChange={handleUserIdChange}
                className="form-input"
                placeholder="Ingrese el ID del usuario"
                min="1"
                required
                aria-describedby="userId-help"
              />
              <div id="userId-help" className="form-help">
                Ingrese el ID num√©rico del usuario que desea eliminar
              </div>
            </div>

            {userToDelete && (
              <>
                <UserPreview user={userToDelete} />

                <div className="info-message">
                  <span aria-hidden="true">‚ÑπÔ∏è</span>
                  Usuario encontrado. Revise la informaci√≥n y confirme si desea proceder con la eliminaci√≥n.
                </div>
              </>
            )}

            <div className="form-actions">
              {!userToDelete ? (
                <button
                  type="submit"
                  className="search-button"
                  disabled={isSearching || !userId.trim()}
                >
                  {isSearching ? (
                    <>
                      <div className="spinner" aria-hidden="true"></div>
                      Buscando...
                    </>
                  ) : (
                    <>
                      <span aria-hidden="true">üîç</span>
                      Buscar Usuario
                    </>
                  )}
                </button>
              ) : (
                <button
                  type="button"
                  onClick={handleShowConfirmation}
                  className="delete-button"
                >
                  <span aria-hidden="true">üóëÔ∏è</span>
                  Eliminar Usuario
                </button>
              )}
            </div>
          </form>
        ) : (
          <ConfirmationDialog
            user={userToDelete}
            confirmationText={confirmationText}
            onConfirmationChange={handleConfirmationTextChange}
            onConfirm={handleDeleteUser}
            onCancel={handleCancelConfirmation}
            isDeleting={isDeleting}
          />
        )}
      </div>
    </div>
  )
}

export default Eliminar